import React from "react";
import Link from "gatsby-link";
import Preview from '../../components/Preview'
import Helmet from "react-helmet";
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faAngleLeft from '@fortawesome/fontawesome-free-solid/faAngleLeft';

export default function Dumbbells({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Инвентарь - Гантели`} />
      <div className="page-top-banner">
        <h2 className="page-heading" style={dumbellsStyle}> Упражнения с гантелями</h2>
        <Link to="/" className="page-btn-back"><FontAwesomeIcon icon={faAngleLeft} className="preview-btn-icon" /> Все упражнения</Link>
      </div>
      {posts
      .filter(post => post.node.frontmatter.filterinventory === 'dumbbells').map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const dumbellsStyle = {
	backgroundImage: 'url(https://www.bodyboss.ru/upload/resize_cache/iblock/ca5/1110_624_2/ca5aa884565523cdcc9006440dc32b45.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query DumbbellsQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



